import React from 'react'
import { Typography } from '@mui/material'
import ListWithTitle from '../../../components/ListWithTitle/ListWithTitle'
import { useAuth } from '../../../contexts/auth/AuthContext'
import AuthButton from '../../../components/AuthButton'
import { StyledButton } from '../../../components/StyledButtons'
import { useNavigate } from 'react-router-dom'
import './styles.scss'

const TabInfo = () => {
  const { logout, user, isUserExist } = useAuth()
  const navigate = useNavigate()

  return (
    <div className="profile-info">
      <Typography variant="h4" component="h1" fontWeight={500} textAlign="center">
         
      </Typography>

      {isUserExist ? (
        <ListWithTitle
          options={[
            {
              title: '',
              text: user.name,
            },
            {
              title: '',
              text: (
                <>
                  {user.email}
                  {/*{user.isEmailConfirmed ? ' ' : <StyledButton onClick={() => navigate('')}> </StyledButton>}*/}
                </>
              ),
            },
            // {
            //   title: '',
            //   text: '',
            // },
          ]}
          className="bordered-box"
        />
      ) : <AuthButton />}

      <div className="profile-info__toolbar">
        {user.role === 'Admin' && <StyledButton fullWidth variant="contained" onClick={() => navigate('/admin/products')}></StyledButton>}

        {isUserExist && <StyledButton fullWidth variant="contained" onClick={logout}></StyledButton>}
      </div>
    </div>
  )
}

export default TabInfo
