import React, { useState } from 'react'
import { object, string, TypeOf } from 'zod'
import { FormProvider, SubmitHandler, useForm } from 'react-hook-form'

import { zodResolver } from '@hookform/resolvers/zod'
import { Box, Stack, Typography, useMediaQuery } from '@mui/material'

import { useAuth } from '../../contexts/auth/AuthContext'
import { useOrders } from '../../contexts/orders/OrdersContext'
import { CreateOrderPayload } from '../../contexts/orders/types'
import { useCart } from '../../contexts/cart/CartContext'
import { StyledLoadingButton } from '../../components/StyledButtons'
import { validationErrors } from '../../helpers/validationErrors'
import ListWithTitle from '../../components/ListWithTitle/ListWithTitle'
import FormInputText from '../../components/FormInputs/Text/FormInputText'
import FormInputMasked from '../../components/FormInputs/Text/FormInputMasked'
import './styles.scss'
import { formatPrice } from '../../helpers/formatters/formatPrice'
import { removePhoneMask } from '../../helpers/formatters/formatPhone'
import { local } from '../../App'

const checkoutSchema = object({
  // user data
  surname: string({ required_error: validationErrors.required('') })
    .nonempty(validationErrors.required(''))
    .min(2, validationErrors.min('', 2))
    .max(32, validationErrors.max('', 32)),
  name: string({ required_error: validationErrors.required('') })
    .nonempty(validationErrors.required(''))
    .min(2, validationErrors.min('', 2))
    .max(32, validationErrors.max('', 32)),
  lastname: string({ required_error: validationErrors.required('') })
    .nonempty(validationErrors.required(''))
    .min(2, validationErrors.min('', 2))
    .max(32, validationErrors.max('', 32)),
  email: string({ required_error: validationErrors.required('') })
    .nonempty(validationErrors.required(''))
    .email(validationErrors.email()),
  phone: string({ required_error: validationErrors.required('') })
    .regex(new RegExp('^((8|\\+7)[\\- ]?)?(\\(?\\d{3}\\)?[\\- ]?)?[\\d\\- ]{7,10}$'), '  '),
  // address
  region: string({ required_error: validationErrors.required('') })
    .nonempty(validationErrors.required('')),
  city: string({ required_error: validationErrors.required('') })
    .nonempty(validationErrors.required('')),
  street: string({ required_error: validationErrors.required('') })
    .nonempty(validationErrors.required('')),
  house: string({ required_error: validationErrors.required('') })
    .nonempty(validationErrors.required('')),
  apartment: string({ required_error: validationErrors.required('') })
    .nonempty(validationErrors.required('')),
  comment: string().max(128, validationErrors.max('', 128)).optional(),
})

type CheckoutInput = TypeOf<typeof checkoutSchema>;

const CheckoutForm: React.FC = () => {
  const [loading, setLoading] = useState<boolean>(false)

  const mobile = useMediaQuery('(max-width:750px)')
  const { createOrder } = useOrders()
  const { user, getUser, isUserExist } = useAuth()
  const { cart, getLocalCartItems } = useCart()

  const methods = useForm<CheckoutInput>({
    resolver: zodResolver(checkoutSchema),
    // defaultValues: {
    //   surname: '',
    //   name: '',
    //   lastname: '',
    //   email: 'tattoona.matata.shop@gmail.com',
    //   phone: '79132537745',
    //   region: '',
    //   city: '',
    //   apartment: '35',
    //   house: '43',
    //   street: '',
    //   comment: ` `,
    // },
  })

  const {
    handleSubmit,
  } = methods

  const onSubmitHandler: SubmitHandler<CheckoutInput> = (data) => {
    setLoading(true)

    const payload: CreateOrderPayload = {
      ...data,
      status: 1,
      price: cart.totalPrice,
      user_id: user.id,
      phone: removePhoneMask(data.phone),
      cart: cart.items,
    }

    createOrder(payload)
      .then(() => {
        if (isUserExist) {
          return getUser(user.id)
        } else {
          local.setItem('cart', JSON.stringify({ items: [] }))
          getLocalCartItems()
        }
      })
      .finally(() => {
        setLoading(false)
      })
  }

  return (
    <Box
      component="form"
      className="checkout-form"
      noValidate
      autoComplete="off"
      onSubmit={handleSubmit(onSubmitHandler)}
    >
      <Typography variant="h5" component="h3" fontWeight={500}>
          
      </Typography>

      <FormProvider {...methods}>
        <Stack className="checkout-form__user" direction={mobile ? 'column' : 'row'} spacing={2}>
          <FormInputText sx={{ mb: 0 }} name="surname" label=""/>

          <FormInputText sx={{ mb: 0 }} name="name" label=""/>

          <FormInputText sx={{ mb: 0 }} name="lastname" label=""/>

          <FormInputText sx={{ mb: 0 }} name="email" label="" type="email"/>

          <FormInputMasked name="phone" label="" mask="+7\ (999) 999-99-99"/>
        </Stack>

        <Typography variant="h5" component="h3" fontWeight={500}>
          
        </Typography>

        <div className="checkout-form__delivery-description">
          <p>
                 - .
          </p>
          <p>
                 .
          </p>
        </div>

        <Stack className="checkout-form__delivery" direction={mobile ? 'column' : 'row'} spacing={2}>
          <FormInputText sx={{ mb: 0 }} name="region" label=""/>

          <FormInputText sx={{ mb: 0 }} name="city" label=""/>

          <FormInputText sx={{ mb: 0 }} name="street" label=""/>

          <FormInputText sx={{ mb: 0 }} name="house" label=""/>

          <FormInputText sx={{ mb: 0 }} name="apartment" label=""/>
        </Stack>

        <FormInputText
          name="comment"
          label=""
          className="checkout-form__delivery-comment"
          rows={4}
          sx={{ mb: 2, mt: 2 }}
          multiline
        />
      </FormProvider>

      <ListWithTitle
        options={[
          {
            title: '',
            text: formatPrice(cart.totalPrice),
          },
        ]}
      />

      <StyledLoadingButton
        variant="contained"
        className="checkout-form__button"
        fullWidth
        type="submit"
        loading={loading}
        sx={{ py: '0.8rem', mt: '1rem' }}
      >
         
      </StyledLoadingButton>
    </Box>
  )
}

export default CheckoutForm
